.block {
  margin-top: 13px;
  width: 100%;
  height: 207px;
  background-color: #16192a;
}

.block .blockHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 15px;
}

.block .blockHeader .title {
    font-size: 13px;
    font-weight: 500;
    color: #656678;
}

.block .blockHeader .actions {
  margin-top: 5px;
  width: 20px;
  cursor: pointer;
}

.block .items {
  padding-left: 15px;
}